                containscount<br />
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;count thancount
                less
              </template>
              <i class="el-icon-question" />
            </el-tooltip>
          </el-col>
          <el-col :span="3">
            <span>:</span>
          </el-col>
          <el-col :span="8" :pull="1">
            <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4 }"
              placeholder=""
              v-model="caseDetail.save_nodes"
              clearable
            >
            </el-input>
          </el-col>
          <el-col :span="1" :pull="1">
            <el-tooltip class="item" effect="dark" placement="right-start">
              <template slot="content">
                jsonPath(),:<br />
                jsonPathkey,;<br />
                ($.data.orderNo:orderNo;$.data.price:price)
              </template>
              <i class="el-icon-question" />
            </el-tooltip>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="4">
            <span>: <span style="color:red">*</span></span>
          </el-col>
          <el-col :span="8" :pull="2">
            <el-select
              placeholder=":"
              v-model="caseDetail['request_type']"
              clearable
              filterable
            >
              <el-option
                v-for="item in requestList"
                :key="item.label"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="4">
            <span>: <span style="color:red">*</span></span>
          </el-col>
          <el-col :span="8" :pull="2">
            <el-select
              placeholder=":"
              v-model="caseDetail['auto']"
              clearable
              filterable
            >
              <el-option
                v-for="item in autoList"
                :key="item.label"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="4">
            <span>: <span style="color:red">*</span></span>
          </el-col>
          <el-col :span="8" :pull="2">
            <el-input
              placeholder=""
              v-model="caseDetail.create_person"
              clearable
            >
            </el-input>
          </el-col>
          <el-col :span="4">
            <span>: <span style="color:red">*</span></span>
          </el-col>
          <el-col :span="8" :pull="2">
            <el-select
              placeholder=""
              v-model="caseDetail.version_id"
              clearable
              filterable
              @focus="checkParentForDialog('version')"
              @change="$forceUpdate"
            >
              <el-option
                v-for="item in versionList"
                :key="item.label"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
