<template>
  <div>
    <div>
      <el-row style="margin-top: -10px">
        <el-col :span="3">
          <span></span>
        </el-col>
        <el-col :span="4" :pull="1">
          <el-select
            v-model="projectId"
            filterable
            placeholder=""
            clearable
            size="mini"
            @change="getConditions"
          >
            <el-option
              v-for="item in projectList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="3">
          <span></span>
        </el-col>
        <el-col :span="3" :pull="1">
          <el-select
            v-model="versionId"
            filterable
            placeholder=""
            clearable
            size="mini"
            @focus="checkParent"
          >
            <el-option
              v-for="item in versionList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="3">
          <span></span>
        </el-col>
        <el-col :span="4" :pull="1">
          <el-select
            v-model="businessId"
            filterable
            placeholder=""
            clearable
            size="mini"
            @focus="checkParent"
          >
            <el-option
              v-for="item in businessList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="2">
          <span>&nbsp;&nbsp;&nbsp;</span>
        </el-col>
        <el-col :span="2">
          <el-select
            v-model="caseType"
            filterable
            placeholder=""
            clearable
            size="mini"
            style="width: 100px"
          >
            <el-option
              v-for="item in caseTypeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
      </el-row>
      <el-row style="margin-top: 0px">
        <el-col :span="2">
          <span>&nbsp;&nbsp;&nbsp;</span>
        </el-col>
        <el-col :span="8">
          <el-select
            v-model="urlId"
            filterable
            placeholder=""
            clearable
            size="mini"
            @focus="checkParent"
            style="width:320px"
          >
            <el-option
              v-for="item in urlList"
              :key="item.value"
              :label="item.label + ':' + urlDict[item.value]"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="2">
          <span>&nbsp;&nbsp;&nbsp;</span>
        </el-col>
        <el-col :span="6">
          <el-date-picker
            v-model="timeRange"
            type="daterange"
            align="right"
            unlink-panels
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            :picker-options="pickerOptions"
            value-format="yyyy-MM-dd"
            size="mini"
          >
          </el-date-picker>
        </el-col>
        <el-col :span="4" :push="3">
          <el-button type="primary" round @click="getValue" size="mini"
            >
          </el-button>
          <el-button type="info" round @click="reset" size="mini"
            >
          </el-button>
        </el-col>
      </el-row>
    </div>

    <div
      id="trendChart"
      style="border-style: solid;border-width: 1px;margin-bottom: 20px;background-color: #fff;border-color:#ebeef5;margin-top: 30px;"
    ></div>
  </div>
</template>

<script>
export default {
  name: 'CaseTrendChart',
  data() {
    return {
      caseTypeList: [],
      caseType: '',
      projectList: [],
      projectId: '',
      versionList: [],
      versionId: '',
      businessList: [],
      businessId: '',
      urlList: [],
      urlDict: {},
      urlId: '',
      timeRange: [],
      pickerOptions: this.GLOBAL.pickerOptions,
      chart: {
        credits: { text: '' },
        yAxis: {
          title: {
            text: '%'
          }
        },
        // 
        title: {
          text: ''
        },
        // :
        xAxis: {
          categories: []
        },
        plotOptions: {
          series: {
            stacking: 'normal'
          }
        },
        labels: {
          items: [
            {
              html: '/',
              style: {
                left: '0px',
                top: '-40px',
                color:
                  (this.Highcharts.theme && this.Highcharts.theme.textColor) ||
                  'black'
              }
            }
          ]
        },
        series: [
          {
            type: 'column',
            name: '',
            tooltip: {
              valueSuffix: ' '
            },
            data: []
          },
          {
            type: 'column',
            name: '',
            tooltip: {
              valueSuffix: ' '
            },
            data: []
          },
          {
            type: 'spline',
            name: '',
            tooltip: {
              valueSuffix: ' %'
            },
            data: [],
            marker: {
              lineWidth: 2,
              lineColor: this.Highcharts.getOptions().colors[3],
              fillColor: 'white'
            }
          },
          {
            type: 'pie',
            name: '',
            data: [
              ['', 0],
              ['', 0]
            ],
            center: [20, 10],
            size: 100,
            showInLegend: false,
            dataLabels: {
              enabled: false
            }
          }
        ]
      }
    };
  },
  methods: {
    // 
    reset() {
      this.initConditions();
      this.getConditions();
      this.getValue();
    },
    // tab
    searchForTab() {
      this.getConditionsForTab();
      this.getValue();
    },
    // 
    initConditions() {
      this.projectId = '';
      this.versionId = '';
      this.businessId = '';
      this.urlId = '';
      this.caseType = '';
      this.timeRange = [];
    },
    // 
    checkParent() {
      if (this.projectId === '' || this.projectId === undefined) {
        this.$message({
          type: 'error',
          message: '!'
        });
      }
    },
    // 
    getConditions() {
      this.businessId = '';
      this.versionId = '';
      this.urlId = '';
      let url = String.format(
        '{0}/caseInterface/caseConditions?project_id={1}',
        this.GLOBAL.MONITOR_URL,
        this.projectId
      );
      this.axios
        .get(url)
        .then(response => {
          if (response.data.code !== '200') {
            this.$message.error(response.data.msg);
          }
          this.projectList = response.data.data.projectList;
          this.versionList = response.data.data.versionList;
          this.businessList = response.data.data.businessList;
          this.urlList = response.data.data.interfaceList;
          this.urlDict = response.data.data.interfaceDict;
          this.caseTypeList = response.data.data.caseTypeList;
          this.useTimeList = response.data.data.useTimeList;
          this.statusList = response.data.data.statusList;
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    // tab
    getConditionsForTab() {
      let url = String.format(
        '{0}/caseInterface/caseConditions?project_id={1}',
        this.GLOBAL.MONITOR_URL,
        this.projectId
      );
      this.axios
        .get(url)
        .then(response => {
          if (response.data.code !== '200') {
            this.$message.error(response.data.msg);
          }
          this.projectList = response.data.data.projectList;
          this.versionList = response.data.data.versionList;
          this.businessList = response.data.data.businessList;
          this.urlList = response.data.data.interfaceList;
          this.urlDict = response.data.data.interfaceDict;
          this.caseTypeList = response.data.data.caseTypeList;
          this.useTimeList = response.data.data.useTimeList;
          this.statusList = response.data.data.statusList;
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    getValue() {
      const loading = this.$loading({
        lock: true,
        text: '~~~',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      let startTime = '';
      let endTime = '';
      if (this.timeRange != null && this.timeRange.length > 0) {
        startTime = this.timeRange[0];
        endTime = this.timeRange[1];
      }
      // 
      let config = {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      };
      let url = String.format(
        '{0}/caseInterface/caseResultPic',
        this.GLOBAL.MONITOR_URL
      );
      // let url = String.format('{0}/caseInterface/caseResultPic', 'http://127.0.0.1:5000')
      let conditions = {
        start_date: startTime,
        end_date: endTime,
        case_type: this.caseType,
        status: this.status,
        project_id: this.projectId,
        version_id: this.versionId,
        business_id: this.businessId,
        url_id: this.urlId
      };
      this.axios
        .post(url, JSON.stringify(conditions), config)
        .then(response => {
          if (response.data.code !== '200') {
            this.$message.error(response.data.msg);
          }
          this.chart.xAxis.categories = response.data.date_list;
          this.chart.series[0].data = response.data.success_data;
          this.chart.series[1].data = response.data.fail_data;
          this.chart.series[2].data = response.data.success_rate;
          this.chart.series[3].data = response.data.pie_data;
          this.Highcharts.chart('trendChart', this.chart);
          loading.close();
          return response;
        })
        .catch(response => {
          loading.close();
          return response;
        });
    }
  }
  // ,
  // beforeMount() {
  //   this.getConditions();
  //   this.getValue();
  // }
};
</script>

<style scoped>
.el-row {
  line-height: 40px;
  margin-top: 0px;
}
</style>



