            placeholder=""
            filterable
            size="mini"
            @change="changeProject(conditionDict.project_id, 'list')"
          >
            <el-option
              v-for="item in projectList"
              :key="item.label"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-select
            v-model="conditionDict.version_id"
            clearable
            placeholder=""
            filterable
            size="mini"
            @focus="checkParent('list')"
          >
            <el-option
              v-for="item in versionList"
              :key="item.label"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-row>
      <el-row style="margin-top: -20px;">
        <el-form-item>
          <el-button
            type="primary"
            round
            size="mini"
            @click="getListByCondition('search')"
            >
          </el-button>
          <el-button type="success" round size="mini" @click="openAddDialog"
            >
          </el-button>
          <el-button type="info" round size="mini" @click="reset"
            >
          </el-button>
        </el-form-item>
      </el-row>
    </el-form>
    <el-table
      :data="tableData"
      :row-class-name="tableRowClassName"
      :header-cell-style="getHeaderClass"
      border
      style="width: 100%"
      size="mini"
    >
      <el-table-column
        fixed
        prop="id"
        label="id"
        align="center"
        width="100px"
        show-overflow-tooltip
      />
      <el-table-column
        prop="pool_name"
        label=""
        align="center"
        width="300px"
        show-overflow-tooltip
      />
      <el-table-column
        prop="project"
