        width="200px"
        show-overflow-tooltip
      />
      <el-table-column
        prop="update_time"
        label=""
        align="center"
        width="200px"
        show-overflow-tooltip
      />
      <el-table-column
        fixed="right"
        label=""
        align="center"
        min-width="200px"
      >
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="getPool(scope.row.id)"
            >
          </el-button>

          <el-button type="text" size="small" @click="deletePool(scope.row.id)"
            >
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      :current-page.sync="currentPage"
      :page-sizes="pageSizes"
      :page-size="pageSize"
      :total="total"
      layout="total, sizes, prev, pager, next, jumper"
      style="margin-top: 10px"
      @size-change="handleSizeChange"
      @current-change="getListByCondition"
    />
    <el-dialog
      :visible.sync="addPoolDialog"
      :before-close="closeAddDialog"
      title=""
      width="90%"
    >
      <el-card>
        <el-form ref="addForm" :model="addForm" :rules="rules" :inline="true">
          <el-row :gutter="10">
            <el-col :span="8">
              <el-form-item label=":" prop="pool_name">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 2, maxRows: 4 }"
                  v-model="addForm.pool_name"
                  placeholder=""
                  size="mini"
                  clearable
                  @change="$forceUpdate()"
                  style="width:200px"
                />
              </el-form-item>
            </el-col>
            <el-col :span="7" :pull="1">
              <el-form-item label=":" prop="project_id">
                <el-select
                  v-model="addForm.project_id"
                  clearable
                  placeholder=""
                  filterable
                  size="mini"
                  @change="changeProject(addForm.project_id, 'add')"
                >
                  <el-option
                    v-for="item in addProjectList"
                    :key="item.label"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6" :pull="1">
              <el-form-item label=":" prop="version_id">
                <el-select
                  v-model="addForm.version_id"
                  clearable
                  placeholder=""
                  filterable
                  size="mini"
                  @focus="checkParent('add')"
                  @change="$forceUpdate()"
                >
                  <el-option
                    v-for="item in addVersionList"
                    :key="item.label"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="3" :pull="1">
              <el-form-item>
                <el-button type="text" @click="clickInit('add')"
                  >
                </el-button>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item
                label=":(Json,JsonList)"
                prop="constant"
              >
                <code-mirror
                  v-model="addForm.constant"
                  :options="cmOptions"
                  class="codemirror"
                  style="width: 600px;height: 100%;"
                />
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item
                label=":(python3,)"
                prop="inner_func"
              >
                <code-mirror
                  v-model="addForm.inner_func"
                  :options="cmOptions"
                  class="codemirror"
                  style="width: 600px;height: 100%;"
                />
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-card>

      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="closeAddDialog"> </el-button>
        <el-button size="mini" type="primary" @click="addPool('addForm')"
          > 
        </el-button>
      </div>
    </el-dialog>
    <el-dialog
      :visible.sync="editPoolDialog"
      :before-close="closeEditDialog"
      :title="'(id:' + editForm.id + ')'"
      width="90%"
    >
      <el-card>
        <el-form
          ref="editForm"
          :model="editForm"
          :rules="editRules"
          :inline="true"
        >
          <el-row :gutter="10">
            <el-col :span="8">
              <el-form-item label=":" prop="pool_name">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 2, maxRows: 4 }"
                  v-model="editForm.pool_name"
                  placeholder=""
                  size="mini"
                  clearable
                  @change="$forceUpdate()"
                  style="width:200px"
                />
              </el-form-item>
            </el-col>
            <el-col :span="7" :pull="1">
              <el-form-item label=":" prop="project_id">
                <el-select
                  v-model="editForm.project_id"
                  clearable
                  placeholder=""
                  filterable
                  size="mini"
                  @change="changeProject(editForm.project_id, 'edit')"
                >
                  <el-option
                    v-for="item in editProjectList"
                    :key="item.label"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6" :pull="1">
              <el-form-item label=":" prop="version_id">
                <el-select
                  v-model="editForm.version_id"
                  clearable
                  placeholder=""
                  filterable
                  size="mini"
                  @focus="checkParent('edit')"
                  @change="$forceUpdate()"
                >
                  <el-option
                    v-for="item in editVersionList"
                    :key="item.label"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="3" :pull="1">
              <el-form-item>
                <el-button type="text" @click="clickInit('edit')"
                  >
                </el-button>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item
                label=":(Json,JsonList)"
                prop="constant"
              >
                <code-mirror
                  v-model="editForm.constant"
                  :options="cmOptions"
                  class="codemirror"
                  style="width: 600px;height: 100%;"
                />
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item
                label=":(python3,)"
                prop="inner_func"
              >
                <code-mirror
                  v-model="editForm.inner_func"
                  :options="cmOptions"
                  class="codemirror"
                  style="width: 600px;height: 100%;"
                  placeholder="shuru"
                />
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-card>

      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="closeEditDialog"> </el-button>
        <el-button size="mini" type="primary" @click="editPool('editForm')"
          > 
        </el-button>
      </div>
    </el-dialog>
    <el-drawer
      title=""
      :before-close="cancelForm"
      :visible.sync="initDataDialog"
      direction="rtl"
      custom-class="demo-drawer"
      size="40%"
    >
      <el-card>
        <div class="demo-drawer__content">
          <el-form
            :model="initDataForm"
            label-position="right"
            label-width="100px"
            :rules="initRules"
            ref="initData"
          >
            <el-form-item label="host" prop="host">
              <el-select
                v-model="initDataForm.host"
                filterable
                allow-create
                clearable
                placeholder="host,:https://privapp.qiyuesuo.me"
                size="mini"
                style="width:400px"
              >
                <el-option
                  v-for="item in hostList"
                  :key="item.value"
                  :label="item.label + ':' + item.address"
                  :value="item.address"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="token" prop="token">
              <el-input
                style="width:400px"
                v-model="initDataForm.token"
                size="mini"
                clearable
                placeholder="token,:695d370b-cb54-400a-8678-0baed9029272"
              ></el-input>
            </el-form-item>
          </el-form>
          <div
            class="demo-drawer__footer"
            style="float: right;margin-bottom: 20px"
          >
            <el-button @click="cancelForm" size="mini"> </el-button>
            <el-button size="mini" type="primary" @click="initData('initData')"
              > 
            </el-button>
          </div>
        </div>
      </el-card>
      <div style="margin-top: 20px;margin-left: 20px;margin-right: 20px">
        <font color="red">{{ help }}</font>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import { codemirror } from 'vue-codemirror';
import 'codemirror/mode/javascript/javascript'; // js
import 'codemirror/addon/selection/active-line'; // styleActiveLinetrue
import 'codemirror/keymap/sublime'; // sublime
import 'codemirror/mode/css/css';
import 'codemirror/theme/monokai.css'; // thememonokai

export default {
  name: 'ParamPoolConfig',
  components: {
    codeMirror: codemirror
  },
  data() {
    return {
      help:
        'token,,,,' +
        '(token:,' +
        ',),4.2.5--4.3.0,' +
        ':4.2.74.2.8ofd,,',
      initType: 'add',
      hostList: [],
      initDataDialog: false,
      initDataForm: {
        token: '',
        host: ''
      },
      cmOptions: {
        tabSize: 2,
        mode: 'application/json',
        theme: 'monokai',
        lineNumbers: true,
        line: true,
        // 
        gutters: ['CodeMirror-foldgutter'],
        // 
        lineWrapping: true,
        // 
        styleActiveLine: true
      },
      addForm: {
        project_id: '',
        version_id: '',
        pool_name: '',
        inner_func: '',
        constant: ''
      },
      editForm: {
        id: '',
        project_id: '',
        version_id: '',
        pool_name: '',
        inner_func: '',
        constant: ''
      },
      addPoolDialog: false,
      editPoolDialog: false,
      currentPage: 1,
      projectList: [],
      versionList: [],
      addProjectList: [],
      editProjectList: [],
      addVersionList: [],
      editVersionList: [],
      conditionDict: {
        id: '',
        pool_name: '',
        project_id: '',
        version_id: ''
      },
      pageSize: 10,
      pageSizes: [5, 10, 20],
      total: 0,
      tableData: [],
      rules: {
        pool_name: [{ required: true, message: '' }],
        project_id: [{ required: true, message: '' }],
        version_id: [{ required: true, message: '' }]
      },
      editRules: {
        pool_name: [{ required: true, message: '' }],
        project_id: [{ required: true, message: '' }],
        version_id: [{ required: true, message: '' }]
      },
      initRules: {
        host: [{ required: true, message: 'host' }],
        token: [{ required: true, message: 'token' }]
      }
    };
  },
  methods: {
    // 
    openAddDialog() {
      this.addPoolDialog = true;
      this.getConditions('add');
    },
    // 
    checkParent(type) {
      if (type === 'list' && this.conditionDict.project_id === '') {
        this.$message({
          type: 'error',
          message: '!'
        });
      } else if (type === 'add' && this.addForm.project_id === '') {
        this.$message({
          type: 'error',
          message: '!'
        });
      } else if (type === 'edit' && this.editForm.project_id === '') {
        this.$message({
          type: 'error',
          message: '!'
        });
      }
    },
    // 
    changeProject(projectId, type) {
      if (type === 'add') {
        this.addForm.version_id = '';
      } else if (type === 'edit') {
        this.editForm.version_id = '';
      } else if (type === 'list') {
        this.conditionDict.version_id = '';
      }
      let url = String.format(
        '{0}/projectInterface/projectVersions?project_id={1}',
        this.GLOBAL.MONITOR_URL,
        projectId
      );
      this.axios
        .get(url)
        .then(response => {
          if (type === 'list') {
            this.versionList = response.data.version_list;
          } else if (type === 'add') {
            this.addVersionList = response.data.version_list;
          } else if (type === 'edit') {
            this.editVersionList = response.data.version_list;
          }

          return response;
        })
        .catch(response => {
          return response;
        });
      this.$forceUpdate();
    },
    // 
    getConditions(type) {
      let url = String.format(
        '{0}/projectInterface/projectListConditions',
        this.GLOBAL.MONITOR_URL
      );
      this.axios
        .get(url)
        .then(response => {
          if (type === 'list') {
            this.projectList = response.data.project_list;
          } else if (type === 'add') {
            this.addProjectList = response.data.project_list;
          } else if (type === 'edit') {
            this.editProjectList = response.data.project_list;
          }

          return response;
        })
        .catch(response => {
          return response;
        });
    },
    // 
    reset() {
      this.conditionDict = {
        id: '',
        pool_name: '',
        project_id: '',
        version_id: ''
      };
      this.versionList = [];
      this.pageSize = 10;
      this.pageSizes = [5, 10, 20];
      this.currentPage = 1;
      this.getConditions('list');
      this.getListByCondition('search');
    },
    // 
    closeAddDialog() {
      this.addPoolDialog = false;
      this.addProjectList = [];
      this.$refs['addForm'].resetFields();
    },
    // 
    closeEditDialog() {
      this.editPoolDialog = false;
      this.editProjectList = [];
      this.$refs['editForm'].resetFields();
    },
    // 
    addPool(formName) {
      if (!this.GLOBAL.checkJson(this.addForm.constant)) {
        this.$message.error('Json!');
      } else {
        this.$refs[formName].validate(valid => {
          if (valid) {
            let url = String.format(
              '{0}/paramPoolInterface/addParamPool',
              this.GLOBAL.MONITOR_URL
            );
            // 
            let config = {
              headers: {
                'Content-Type': 'application/json;charset=UTF-8'
              }
            };
            this.axios
              .post(url, JSON.stringify(this.addForm), config)
              .then(response => {
                if (response.data.code === '200') {
                  this.closeAddDialog();
                  this.$message.success('');
                  this.getListByCondition('search');
                } else {
                  this.$message.error(response.data.msg);
                }
                return response;
              })
              .catch(response => {
                return response;
              });
          }
        });
      }
    },
    // id
    getPool(poolId) {
      this.editPoolDialog = true;
      this.getConditions('edit');
      let url = String.format(
        '{0}/paramPoolInterface/paramPoolInfo',
        this.GLOBAL.MONITOR_URL
      );
      // 
      let config = {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      };
      let params = {
        id: poolId
      };
      this.axios
        .post(url, JSON.stringify(params), config)
        .then(response => {
          this.editForm = response.data.paramPool;
          this.editVersionList = response.data.versionList;
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    // id
    async deletePool(poolId) {
      const result = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err);
      if (result === 'confirm') {
        let url = String.format(
          '{0}/paramPoolInterface/removeParamPool',
          this.GLOBAL.MONITOR_URL
        );
        // 
        let config = {
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          }
        };
        let params = {
          id: poolId
        };
        this.axios
          .post(url, JSON.stringify(params), config)
          .then(response => {
            if (response.data.code === '200') {
              this.$message.success('');
              this.getListByCondition('search');
            } else {
              this.$message.error(response.data.msg);
            }
            return response;
          })
          .catch(response => {
            return response;
          });
      }
    },
    // 
    editPool(formName) {
      if (!this.GLOBAL.checkJson(this.editForm.constant)) {
        this.$message.error('Json!');
      } else {
        this.$refs[formName].validate(valid => {
          if (valid) {
            let url = String.format(
              '{0}/paramPoolInterface/editParamPool',
              this.GLOBAL.MONITOR_URL
            );
            // 
            let config = {
              headers: {
                'Content-Type': 'application/json;charset=UTF-8'
              }
            };
            this.axios
              .post(url, JSON.stringify(this.editForm), config)
              .then(response => {
                if (response.data.code === '200') {
                  this.closeEditDialog();
                  this.$message.success('');
                  this.getListByCondition('search');
                } else {
                  this.$message.error(response.data.msg);
                }
                return response;
              })
              .catch(response => {
                return response;
              });
          }
        });
      }
    },
    // 
    getListByCondition(type) {
      if (type === 'search') {
        this.currentPage = 1;
      }
      let url = String.format(
        '{0}/paramPoolInterface/searchParamPool',
        this.GLOBAL.MONITOR_URL
      );
      // 
      let config = {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      };
      this.conditionDict['pageSize'] = this.pageSize;
      this.conditionDict['page_index'] = this.currentPage;
      this.axios
        .post(url, JSON.stringify(this.conditionDict), config)
        .then(response => {
          this.tableData = response.data.paramPoolList.tableData;
          this.total = response.data.total;
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    // 
    handleSizeChange(val) {
      this.pageSize = val;
      this.currentPage = 1;
      this.getListByCondition();
    },
    // 
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'warning-row';
      } else {
        return 'success-row';
      }
    },
    // 
    getHeaderClass({ rowIndex }) {
      if (rowIndex === 0) {
        return 'background:deepskyblue';
      } else {
        return '';
      }
    },
    // 
    initData(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const loading = this.$loading({
            lock: true,
            text: ',(30)',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          setTimeout(() => {
            loading.close();
            this.initDataDialog = false;
          }, 30000);
          // 
          let config = {
            headers: {
              'Content-Type': 'application/json;charset=UTF-8'
            }
          };
          let url = String.format(
            '{0}/initDataInterface/initBaseData',
            this.GLOBAL.MONITOR_URL
          );
          let conditions = {
            host: this.initDataForm.host,
            token: this.initDataForm.token,
            version_id:
              this.initType === 'add'
                ? this.addForm.version_id
                : this.editForm.version_id
          };
          this.axios
            .post(url, JSON.stringify(conditions), config)
            .then(response => {
              if (response.data.code === '200') {
                loading.close();
                if (this.initType === 'add') {
                  this.addForm.constant = JSON.stringify(
                    response.data.data,
                    null,
                    4
                  );
                } else if (this.initType === 'edit') {
                  this.editForm.constant = JSON.stringify(
                    response.data.data,
                    null,
                    4
                  );
                }
                this.$message.success(response.data.msg);
              } else {
                loading.close();
                this.Message.error(response.data.msg);
              }
              return response;
            })
            .catch(response => {
              return response;
            });
        }
      });
    },
    // 
    cancelForm() {
      this.initDataDialog = false;
      this.$refs['initData'].resetFields();
    },
    // 
    clickInit(type) {
      let url = String.format(
        '{0}/hostInterface/getHostList',
        this.GLOBAL.MONITOR_URL
      );
      this.axios
        .get(url)
        .then(response => {
          this.hostList = response.data.hostList;
          return response;
        })
        .catch(response => {
          return response;
        });
      let version = '';
      if (type === 'add') {
        version = this.addForm.version_id;
      } else if (type === 'edit') {
        version = this.editForm.version_id;
      }
      if (version === '') {
        this.$message.error(',!');
      } else {
        this.initType = type;
        this.initDataDialog = true;
      }
    }
  }
};
</script>

<style scoped>
body .el-table th.gutter {
  display: table-cell !important;
}

body .el-table colgroup.gutter {
  display: table-cell !important;
}

.el-table >>> .warning-row {
  background: oldlace;
}

.el-table >>> .success-row {
  background: #f0f9eb;
}

td:not(.is-hidden):last-child {
  right: -1px;
}

.el-table__header tr,
.el-table__header th {
  padding: 0;
  height: 40px;
}

.el-row {
  line-height: 40px;
  margin-top: 10px;
}
</style>



