<template>
  <div>
    <el-table
      v-loading="loading"
      element-loading-text=",,,30"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
      :data="tabData"
      style="width: 100%;margin-top: -10px"
      border
      size="mini"
      :row-class-name="tableRowClassName"
      width="100%"
    >
      <!--index,key-->
      <template v-for="(col, index) in tabHeader">
        <!--keyhtml,es6-->
        <!---->
        <el-table-column
          v-if="col.prop === 'action'"
          :label="col.label"
          :key="index"
          align="center"
          show-overflow-tooltip
          width="200px"
          fixed="right"
        >
          <template slot-scope="scope">
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button
                type="text"
                size="mini"
                @click="executeCase(tabData[scope.$index].id)"
                >
              </el-button>
            </el-tooltip>
            <el-button
              type="text"
              size="mini"
              @click="editClick(tabData[scope.$index].id)"
              >
            </el-button>
            <el-button
              type="text"
              size="mini"
              @click="deleteCase(tabData[scope.$index].id)"
              >
            </el-button>
            <el-button
              type="text"
              size="mini"
              @click="copyCase(tabData[scope.$index].id)"
              >
            </el-button>
          </template>
        </el-table-column>
        <el-table-column
          v-else-if="col.prop === 'id'"
          :prop="col.prop"
          :label="col.label"
          :key="index"
          align="center"
          show-overflow-tooltip
          fixed="left"
          width="80px"
        >
        </el-table-column>
        <el-table-column
          v-else-if="
            col.prop === 'case_name' ||
              col.prop === 'host' ||
              col.prop === 'url'
          "
          :prop="col.prop"
          :label="col.label"
          :key="index"
          align="center"
          show-overflow-tooltip
          width="250px"
        >
        </el-table-column>
        <el-table-column
          v-else-if="col.prop === 'case_type'"
          :prop="col.prop"
          :label="col.label"
          :key="index"
          align="center"
          show-overflow-tooltip
          width="120px"
        >
        </el-table-column>
        <el-table-column
          v-else
          :prop="col.prop"
          :label="col.label"
          :key="index"
          align="center"
          show-overflow-tooltip
          width="200px"
        >
        </el-table-column>
      </template>
    </el-table>
    <case-dialog
      v-if="dialogVisible"
      :visible="dialogVisible"
      :case-id="editCaseId"
      :type="caseDialogType"
      @listenToChildEvent="callbackFunc"
    ></case-dialog>
  </div>
</template>

<script>
import CaseDialog from './CaseDialog';

export default {
  // 
  name: 'tablePub',
  components: { CaseDialog },
  // (tabData:;tabHeader:;tabHeight:,)
  props: {
    tabData: null,
    tabHeader: null
  },
  data() {
    return {
      dialogVisible: false,
      editCaseId: null,
      caseDialogType: 2,
      loading: false
    };
  },
  methods: {
    // 
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'warning-row';
      } else {
        return 'success-row';
      }
    },
    // 
    executeCase(id) {
      this.loading = true;
      // 
      let config = {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      };
      let url = String.format(
        '{0}/executeInterface/executeCase',
        this.GLOBAL.MONITOR_URL
      );
      let caseEntity = {
        id: id
      };
      this.axios
        .post(url, JSON.stringify(caseEntity), config)
        .then(response => {
          let responseCode = response.data.code;
          this.loading = false;
          if (responseCode === '200') {
            this.Message.success('');
          } else {
            this.Message.error(response.data.msg);
          }
          return response;
        })
        .catch(response => {
          return response;
        });
      setTimeout(() => {
        if (this.loading) {
          this.loading = false;
          this.$message.error(
            ','
          );
        }
      }, 30000);
    },
    // 
    editClick(id) {
      this.editCaseId = id;
      this.caseDialogType = 2;
      this.dialogVisible = true;
    },
    // 
    copyCase(id) {
      this.editCaseId = id;
      this.caseDialogType = 3;
      this.dialogVisible = true;
    },
    deleteCase(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          let caseInfo = { id: id };
          let url = String.format(
            '{0}/caseInterface/removeCase',
            this.GLOBAL.MONITOR_URL
          );
          // 
          let config = {
            headers: {
              'Content-Type': 'application/json;charset=UTF-8'
            }
          };
          this.axios
            .post(url, JSON.stringify(caseInfo), config)
            .then(response => {
              let responseCode = response.data.code;
              if (responseCode === '200') {
                this.Message.success('');
              } else {
                this.Message.error(response.data.msg);
              }
              // 
              this.$emit('getCaseList');
              return response;
            })
            .catch(response => {
              // 
              this.$emit('getCaseList');
              return response;
            });
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    },
    callbackFunc(fresh) {
      this.dialogVisible = false;
      if (fresh) {
        this.$emit('getCaseList');
      }
    }
  }
};
</script>
<style scoped>
.el-table >>> .warning-row {
  background: oldlace;
}

.el-table >>> .success-row {
  background: #f0f9eb;
}
</style>



